import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
// Text Editor
import { EditorState, convertToRaw } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import draftToHtml from "draftjs-to-html";
import DOMPurify from "dompurify";
// Dummy DB file writing

const TaskNew = () => {
  const navigate = useNavigate();

  // Convert draft to HTML (in realtime instead of on submit for preview's sake)
  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );

  const [convertedContent, setConvertedContent] = useState(null);

  useEffect(() => {
    let html = draftToHtml(convertToRaw(editorState.getCurrentContent()));
    setConvertedContent(html);
  }, [editorState]);

  function createMarkup(html) {
    return {
      __html: DOMPurify.sanitize(html),
    };
  }

  // handle cancel/submit
  function cancelHandler() {
    alert("Editing canceled.");
    return navigate("/tasks");
  }

  function submitHandler() {
    // TODO: write to db
    console.log(convertedContent);
    alert("Task updated.");
    return navigate("/tasks");
  }

  return (
    <main className="center-page task-page add">
      <h1 className="task-page-h1">New Task</h1>
      <Editor
        editorState={editorState}
        onEditorStateChange={setEditorState}
        wrapperClassName="wrapper-class"
        editorClassName="editor-class"
        toolbarClassName="toolbar-class"
        editorStyle={{ height: "50vh", margin: "0" }}
      />
      <h2 className="preview-h2">Preview</h2>
      <div
        className="preview"
        dangerouslySetInnerHTML={createMarkup(convertedContent)}
      ></div>
      <div className="task-buttons">
        <button className="task-cancel" onClick={cancelHandler}>
          Cancel
        </button>
        <button className="task-submit" onClick={submitHandler}>
          Submit
        </button>
      </div>
    </main>
  );
};

export default TaskNew;
