import React from "react";
import { Link } from "react-router-dom";
import { players } from "../data/users-data";
import { groups } from "../data/groups-data";
import avatar from "../images/pixie-avatar.jpeg";

// todo: change outside border color/thickness if someone is friend or foe

const PlayerMapper = () => {
  return players.map((player) => {
    return (
      <Link to={`/players/${player.id}`}>
        <div className="player" key={player.id}>
          <h3 className="player-name">
            <span className="player-level">{player.level}</span> {player.rank}{" "}
            {player.name}
          </h3>
          <h4 className="player-subheader">{player.group}</h4>
          <img src={avatar} alt="avatar" className="player-avatar" />
          <p className="player-desc">{player.description}</p>
        </div>
      </Link>
    );
  });
};

const Players = () => {
  console.log(groups.filter((group) => console.log(group)));
  return (
    <div>
      <main className="center-page">
        <h2 className="players-h2">Players</h2>
        <div className="players-container">
          <PlayerMapper />
        </div>
      </main>
    </div>
  );
};

export default Players;
