import React from "react";
import { useParams } from "react-router-dom";
import { groups } from "../data/groups-data";

const Group = () => {
  const { groupId } = useParams();
  const group = groups.find((group) => group.id === parseInt(groupId));
  const { name, description, color } = group;

  return (
    <main className="center-page group-main">
      <h2 className="group-h2" style={{ color: `#${color}` }}>
        {name}
      </h2>
      <p className="group-desc">{description}</p>
    </main>
  );
};

export default Group;
