import React from "react";
import { useParams, Link } from "react-router-dom";
import { players } from "../data/users-data";
import avatar from "../images/pixie-avatar.jpeg";

const Player = () => {
  const { playerId } = useParams();
  const player = players.find((player) => player.id === parseInt(playerId));
  const {
    name,
    description,
    group,
    rank,
    level,
    points,
    lastLogin,
    completedTasks,
    createdTasks,
    terms,
    comments,
  } = player;

  const TermsMapper = () => {
    return terms.map((term) => {
      return <span className="profile-term">{term}</span>;
    });
  };

  return (
    <main className="center-page profile-main">
      <h2 className="profile-h2">
        {rank} {name}
      </h2>
      <p className="profile-group">
        Group:{" "}
        <Link to={`/groups/2`} className="profile-group-link">
          {group}
        </Link>{" "}
        🔹 Level: {level}
      </p>
      <p>Last Login: {lastLogin}</p>
      <img src={avatar} alt="avatar" className="profile-avatar" />
      <p>{description}</p>
      <p className="profile-terms">
        Terms: <TermsMapper />
      </p>
      <p>Points: {points}</p>
      <p>Completed Tasks</p>
      <p>Created Tasks</p>
      <p>Comments</p>
    </main>
  );
};

export default Player;
