import React from "react";
import { Link } from "react-router-dom";
import { groups } from "../data/groups-data";

const Groups = () => {
  const GroupMapper = () => {
    return groups.map((group) => {
      return (
        <Link to={`/groups/${group.id}`} className="groups-group">
          <h3 className="groups-name" style={{ color: `#${group.color}` }}>
            {group.name}
          </h3>
          <p>{group.description}</p>
        </Link>
      );
    });
  };

  return (
    <div>
      <main className="center-page groups-main">
        <h2 className="groups-h2">Groups</h2>
        <div className="groups-container">
          <GroupMapper />
        </div>
      </main>
    </div>
  );
};

export default Groups;
